/* style for product list */
.product-list {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(4, calc((100% - 6rem) / 4));
  gap: 2rem;
}

/* style for pagination */
.pagination {
  text-align: right;
}

.pagination-action {
  margin-bottom: 1rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.pagination-action > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination-button {
  padding: 1rem;
  border: 1px solid #e4e4e5;
  cursor: pointer;
}

.pagination-button i {
  font-size: 1.6rem;
}

.pagination-page__number {
  padding: 0.8rem 1.25rem;
  background-color: #000;
  color: #fff;
}

/* style for change screen size */
@media (width < 1200px) {
  .product-list {
    grid-template-columns: repeat(3, calc((100% - 4rem) / 3));
  }
}

@media (width < 992px) {
  .product-list {
    grid-template-columns: repeat(2, calc((100% - 2rem) / 2));
  }
}
