/* style for form authentication */
.form {
  width: var(--width-xlarge);
  margin: 0 auto;
  padding: 4rem 0;

  background-image: url(../../../public/images/banner1.jpg);
  background-position-x: 30%;
  background-size: cover;
}

.container {
  min-width: 50rem;
  width: 40%;
  margin: 0 auto;
  padding: 4rem 3rem;
  text-align: center;
  border: none;
  border-radius: 1rem;
  /* box-shadow: h-offset v-offset blur spread color */
  box-shadow: 0.5rem 0.5rem 2rem 0.5rem rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.container h1 {
  margin-bottom: 6rem;
  color: rgba(0, 0, 0, 0.4);
  font-weight: 400;
}

.container button {
  width: 100%;
  margin-bottom: 4rem;
}

.container ul {
  text-align: left;
  font-size: 1.6rem;
  list-style-type: none;
  color: red;
}

.container ul li {
  margin-bottom: 0.5rem;
}

/* style for form-control */
.form-controls {
  width: 100%;
  margin-bottom: 1.5rem;
}

.form-control {
  width: 100%;
  border: 1px solid #e4e4e5;
  padding: 2rem 0;
  border-collapse: collapse;
}

.form-control input {
  width: var(--width-xlarge);
  border: none;
  line-height: 3.2rem;
  padding: 1rem;
  font-size: 1.8rem;
}

.form-control input:focus {
  outline: none;
}

/* style for form action */
.form-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.form-navigation a {
  text-decoration: none;
  font-size: 1.8rem;
  color: #90a3f3;
}

.form-navigation a:hover,
.form-navigation a:active,
.form-navigation a.active {
  color: #e1bb6e;
}
