/* style for product detail page */
.container {
  width: var(--width-xlarge);
  margin: 5rem auto;
}

/* style for product info */
.product-info {
  width: 100%;
  margin-bottom: 10rem;

  display: flex;
  gap: 2rem;
}

.product-info > div {
  width: calc((100% - 2rem) / 2);
}

/* style for product image */
.product-img {
  display: flex;
  gap: 2rem;
}

.product-img img {
  width: 100%;
  cursor: pointer;
}

.product-img__list {
  width: 15%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.product-img__list > div {
  border: 1px solid transparent;
}

div.product-img__active {
  border: 1px solid #e1bb6e;
}

.product-img__current {
  width: calc(85% - 2rem);
}

/* style for product general info */
.product-info__general {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* style for product order info */
.product-info__order {
  display: flex;
}

.product-info__order > p {
  padding: 1rem;
  border-top: 1px solid #e4e4e5;
  border-bottom: 1px solid #e4e4e5;
  width: 4rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

p.product-quantity__title {
  width: 15rem;
  border-left: 1px solid #e4e4e5;
  justify-content: flex-start;
}

.product-quantity__icon {
  cursor: pointer;
}

.product-button__order {
  font-size: 1.8rem;
  padding: 1rem 3rem;
}

/* style for product description */
.product-description {
  margin-bottom: 8rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

/* style for product related */
.product-related > h3 {
  margin-bottom: 2rem;
}

/* style for change screen size */
@media (width < 768px) {
  .container {
    width: var(--width-full);
  }

  .product-info {
    flex-direction: column;
  }

  .product-info > div {
    width: 100%;
  }
}
