/* style for product */
.section-product {
  width: 100%;
  margin-top: 2rem;
}

.container {
  width: var(--width-xlarge);
  margin: 0 auto;
  padding: 2rem 0;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
}

/* style for detail */
.product-head__detail p {
  margin-bottom: 0.5rem;
}

/* style for product list */
.product-list {
  width: 100%;

  display: grid;
  grid-template-columns: repeat(4, calc((100% - 6rem) / 4));
  gap: 2rem;
}

/* style for other infomation */
.product-services {
  width: 100%;
  background-color: #f8f9fa;
  margin-top: 8rem;
  padding: 8rem;

  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 2rem;
}

.product-services p {
  margin-top: 0.8rem;
}

.product-subscribe {
  padding: 3rem 0;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.subscribe-action {
  display: flex;
  align-items: center;
}

.product-subscribe input[type="email"] {
  border: 2px solid #e4e4e5;
  border-right: none;
  width: 30rem;
  line-height: 3.2rem;
  padding: 1rem;
  font-size: 1.8rem;
}

.product-subscribe input[type="email"]:focus {
  outline: none;
}

/* style for change screen size */
@media (width < 1200px) {
  .product-list {
    grid-template-columns: repeat(3, calc((100% - 4rem) / 3));
  }
}

@media (width < 992px) {
  .product-list {
    grid-template-columns: repeat(2, calc((100% - 2rem) / 2));
  }
}
