@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,400;1,500;1,600&display=swap");
@import url("https://pro.fontawesome.com/releases/v5.10.0/css/all.css");

:root {
  --width-small: 50%;
  --width-medium: 70%;
  --width-large: 80%;
  --width-xlarge: 90%;
  --width-full: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-style: italic;
  min-width: 600px;
}

body {
  font-family: Montserrat, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 3.2rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 2rem;
}

p,
strong {
  font-size: 1.8rem;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 1.5rem 3rem;
  background-color: rgba(0, 0, 0, 0.8);
  border: none;
  color: rgba(255, 255, 255, 0.6);
  font-size: 2.2rem;
  font-style: italic;
  cursor: pointer;
}

.btn:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: rgba(255, 255, 255, 0.8);
}

.btn-lg {
  padding: 3rem;
}

.btn-font-normal {
  font-style: normal;
}

p.text {
  color: rgba(0, 0, 0, 0.4);
  line-height: 3rem;
}

.text-center {
  text-align: center;
}

.form-input {
  border: 2px solid #e4e4e5;
  line-height: 3.2rem;
  padding: 1rem;
  font-size: 1.8rem;
}

.form-input:focus {
  outline: none;
}

/* tu dong xuong hang khi text co \n */
.text-pre-wrap {
  white-space: pre-wrap;
}

.error {
  color: red;
}
