/* style for icon chat */
.icon-chat {
  padding: 0.5rem 0.7rem;
  font-size: 3rem;
  background-color: #000;
  border-radius: 50%;
  cursor: pointer;

  position: fixed;
  bottom: 10%;
  right: 1%;
  z-index: 100;
}

.icon-chat {
  color: #fff;
}

/* ////////////////////////////////// */
/* ////////////////////////////////// */
/* ////////////////////////////////// */

/* style for chat box */
.chat {
  width: 60rem;

  position: fixed;
  bottom: calc(10% + 6rem);
  right: calc(1% + 4.5rem);
  z-index: 100;
}

/* includes header, content and input */
.chat-box {
  width: 100%;
  background-color: #fff;
  border: 1px solid #e4e4e5;
  border-radius: 2rem;
  /* h-offset v-offset blur spread color */
  box-shadow: 0 0 1rem 0.5rem rgba(0, 0, 0, 0.2);
}

.user-img {
  width: 4rem;
  border: none;
  background-color: transparent;
}

.user-img img {
  width: 100%;
  border-radius: 50%;
}

hr {
  border-color: rgba(203, 203, 203, 0.1);
}

/* ////////////////////////////////// */
/* header */
/* ////////////////////////////////// */
.chat-header {
  padding: 2rem;
  font-style: normal;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header button {
  padding: 0.7rem 1.2rem;
  border: none;
  background-color: #e4e4e5;
  color: rgba(0, 0, 0, 0.4);
  font-size: 1.6rem;
  font-style: italic;
  cursor: pointer;
}

.chat-header button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgba(255, 255, 255, 0.6);
}

/* ////////////////////////////////// */
/* content */
/* ////////////////////////////////// */
.chat-content {
  width: 100%;
  height: 45rem;
  padding: 1rem 14.79rem 1rem 1rem;
  overflow: auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.user-content {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.user-content p {
  background-color: #48b0f7;
  color: #fff;
  padding: 1rem;
  border-radius: 0.3rem;
  max-width: 80%;
}

.guest-content {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;
}

.guest-content p {
  background-color: #e4e4e5;
  color: rgba(0, 0, 0, 0.4);
  padding: 1rem;
  border-radius: 0.3rem;
  max-width: 80%;
}

/* ////////////////////////////////// */
/* input */
/* ////////////////////////////////// */
.chat-input {
  padding: 2rem;
  background-color: #f9fafb;
  border-bottom-left-radius: 2rem; /* Điều chỉnh độ cong trái dưới */
  border-bottom-right-radius: 2rem; /* Điều chỉnh độ cong phải dưới */

  display: flex;
  align-items: center;
  gap: 2rem;
}

.chat-input input {
  padding-left: 2px;
  font-size: 2rem;
  line-height: 2.8rem;
  border: 1px inset rgba(203, 203, 203, 0.1);
  background-color: #fff;
}

.chat-input input:focus {
  outline: none;
}

.icon {
  font: 2rem;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.color-blue {
  color: #48b0f7;
}

/* ////////////////////////////////// */
/* animation */
/* ////////////////////////////////// */

@keyframes scale-rotate {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  33% {
    transform: scale(1) rotate(-10deg);
    opacity: 1;
  }
  67% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.chat {
  animation-name: scale-rotate;
  animation-duration: 1.2s;
  animation-timing-function: ease-out;
}
