/* style for checkout */
.checkout {
  width: var(--width-xlarge);
  margin: 0 auto 5rem auto;
}

.checkout h2 {
  margin: 6rem 0 3rem 0;
}

/* style for billing details */
.checkout-billing__details {
  width: 100%;
  display: flex;
  gap: 5rem;

  position: relative;
}

/* style for billing order info */
.billing-order__info {
  width: calc(100% - 55rem);
}

.form-control {
  width: 100%;
  margin: 2rem 0;

  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-control label {
  font-size: 1.8rem;
}

/* style for product order detail */
div.billing-order__products {
  width: 50rem;
}

.products-container {
  padding: 6rem;
  background-color: #f6f9f6;
}

.products-container h2 {
  margin: 0;
  margin-bottom: 4rem;
}

.products-detail > div {
  width: 100%;
  padding: 1rem 0;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.products-detail > div strong {
  width: 45%;
}

/* style for change screen size */
@media (width < 950px) {
  .checkout-billing__details {
    flex-wrap: wrap;
    padding-top: 48rem;
  }

  .checkout-billing__details > div {
    width: 100%;
  }

  .billing-order__products {
    position: absolute;
    left: 0;
    top: 0;
  }
}

@media (width < 768px) {
  .checkout {
    width: 100%;
  }
}
