/* style for backdrop */
.backdrop {
  background-color: rgba(75, 76, 77, 0.8);
  width: 100%;
  height: 100vh;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

/* style for overlay */
.overlay {
  background-color: #fff;

  width: var(--width-large);
  padding: 1rem;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

/* style for item container */
.container {
  display: flex;
  gap: 6rem;
}

.container > div {
  width: calc((100% - 6rem) / 2);
  min-height: 70vh;
}

.container img {
  width: 100%;
  height: 100%;
  vertical-align: top;
}

/* style for description */
.description {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

/* style for button close popup */
.action_hide {
  text-align: right;
}

.action_hide button {
  padding: 1rem;
  border: none;
  background: none;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

.action_hide button:hover {
  background-color: rgba(75, 76, 77, 0.8);
  color: white;
}

/* style for item detail */
.detail {
  padding: 0 5rem 0 2rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* style for button view detail */
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.button i {
  color: #fff;
}
