/* style for banner */
.banner {
  width: 100%;
}

.container {
  width: 100%;
  padding: 6rem 0;
  background-color: #f6f9f6;

  display: flex;
  justify-content: center;
  align-items: center;
}

/* style for info */
.info {
  width: 100%;
  padding: 0 10%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.info a {
  text-decoration: none;
  color: #000;
}

.info a:hover,
.info a:active {
  color: #e1bb6e;
}
