/* style for product */
.product {
  width: 100%;

  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* style for product image */
.product-img {
  width: 100%;
}

.product-img img {
  width: 100%;
  vertical-align: top;
  opacity: 1;
  cursor: pointer;
}

.product-img:hover img {
  opacity: 0.5;
}

/* style for product detail */
.product-detail {
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.product-detail strong {
  display: inline-block;
  line-height: 2rem;
}

.product-detail p {
  color: rgba(0, 0, 0, 0.4);
  line-height: 1rem;
}
