/* style for shop page */
.section-shop {
  width: var(--width-xlarge);
  margin: 0 auto 5rem auto;
}

/* style for navbar & products */

/* style for navbar */
.nav-product {
  margin-top: 5rem;

  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
}

/* style for products */
.products {
  width: calc(75% - 3rem);
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

/* style for filter products */
.product-action {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.product-action input[type="text"] {
  border: 2px solid #e4e4e5;
  width: 30rem;
  line-height: 3.2rem;
  padding: 1rem;
  font-size: 1.8rem;
}

.product-action input[type="text"]:focus {
  outline: none;
}

.product-action div {
  display: flex;
  align-items: center;
}

.product-action select {
  font-size: 1.6rem;
  padding-right: 2rem;
}

.product-action option {
  font-size: 1.6rem;
}

/* style for change screen size */
@media (width < 768px) {
  .section-shop {
    width: var(--width-full);
  }
}
