/* style for navbar of shop page */
.shop-navbar {
  width: 25%;
}

.shop-navbar h3 {
  margin-bottom: 2rem;
}

/* style for nav */
.nav {
  width: 100%;
}

.nav li {
  width: 100%;
  padding: 1rem 1rem 1rem 2.5rem;
}

li.product-classify {
  background-color: #f6f9f6;
}

li.brand {
  background-color: #000;
  color: #fff;
}

/* style for link */
.nav a {
  text-decoration: none;
  color: inherit;
  font-size: 1.8rem;
}

.nav a:hover,
.nav a:active,
.nav a.active {
  color: #e1bb6e;
}
