/* style for section category */
.section-category {
  width: 100%;
  margin-top: 4rem;
}

.container {
  width: var(--width-xlarge);
  margin: 0 auto;
  padding: 2rem 0;

  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  align-items: center;
}

.detail {
  text-align: center;
}

.detail p {
  margin-bottom: 0.5rem;
}

/* style for list of category */
.categories {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.categories > div {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.categories a {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.category-ele {
  width: calc((100% - 4rem) / 3);
  cursor: pointer;
  position: relative;
}

.category-big__ele {
  width: calc(50% - 1rem);
}

/* style for img and hover img */
.categories img {
  opacity: 1;
  width: 100%;
  vertical-align: top;
  transition: opacity 0.2s ease-in-out;
}

.category-ele:hover img {
  opacity: 0.5;
}

/* style for change screen size */

@media (width < 768px) {
  .categories > div {
    justify-content: center;
  }

  .container {
    width: var(--width-large);
  }
}

@media (width < 768px) {
  .category-big__ele,
  .category-ele {
    width: var(--width-xlarge);
  }

  .category-ele {
    height: 305px;
  }

  .category-ele img {
    height: 100%;
  }
}
