/* style for cart page */
.section-cart {
  width: var(--width-xlarge);
  margin: 0 auto 5rem auto;
}

.section-cart h2 {
  margin: 6rem auto 3rem 0;
}

/* style for cart detail */
.cart {
  width: 100%;
  position: relative;

  display: flex;
  justify-content: space-between;
  gap: 5rem;
}

.cart-detail {
  width: calc(100% - 44rem);

  display: flex;
  flex-direction: column;
  gap: 4rem;
}

/* style for table of list products */
.cart-detail table {
  width: 100%;
  border-collapse: collapse;
}

/* thead */
.cart-detail table thead tr {
  background-color: #f6f9f6;
}

.cart-detail table th {
  font-size: 1.8rem;
  padding: 2rem 1rem;
}

/* tbody */

.cart-detail table td {
  text-align: center;
  vertical-align: middle;
  padding: 1rem;
}

/* img */
.cell-img {
  width: 10%;
}

.cart-detail table td img {
  width: 100%;
  height: 100%;
  vertical-align: top;
}

/* name */
.cart-detail table td h4 {
  width: 25%;
}

.cell-name {
  width: 25%;
}

/* style for product quantity */
.product-info__order {
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-info__order > p {
  padding: 1rem;
  width: 3rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

.product-icon {
  cursor: pointer;
}

/* style for cart amount */
.container-amount {
  width: 40rem;
}

.cart-amount {
  padding: 6rem;
  background-color: #f6f9f6;
}

.cart-amount h2 {
  margin: 0;
  margin-bottom: 4rem;
}

.cart-total > div {
  width: 100%;
  padding: 1rem 0;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* style for coupon */
.cart-coupon {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
}

.cart-coupon input[type="text"] {
  border: 2px solid #e4e4e5;
  line-height: 3.2rem;
  padding: 1rem;
  font-size: 1.8rem;
}

.cart-coupon input[type="text"]:focus {
  outline: none;
}

.cart-coupon button i {
  margin-right: 1rem;
  color: #fff;
}

/* style for cart navigation */
.cart-navigation {
  background-color: #f6f9f6;
  padding: 3rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-navigation a {
  text-decoration: none;
  padding: 1.2rem;
  border: 2px solid transparent;

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.cart-navigation a:hover,
.cart-navigation a:active {
  border: 2px solid #000;
  color: #e1bb6e;
}

.cart-navigation a:hover p,
.cart-navigation a:active p {
  color: #e1bb6e;
}

.cart-navigation a i {
  font-size: 1.8rem;
}

/* style for change screen size */
@media (width < 1200px) {
  .cart {
    flex-wrap: wrap;
    padding-bottom: 13rem;
  }

  .cart-detail {
    width: 100%;
  }

  .cart-navigation {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
  }

  .container-amount {
    width: 100%;
  }

  .cart-amount__info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 10rem;
  }

  .cart-amount__info > div {
    width: 100%;
  }

  .cart-total > div {
    padding: 1.3rem 0;
  }

  .cart-coupon {
    margin-top: 0;
  }
}

@media (width < 768px) {
  .section-cart {
    width: var(--width-full);
  }
}
