/* style for banner */
.banner {
  width: 100%;
}

.container {
  width: var(--width-xlarge);
  margin: 0 auto;
  position: relative;
}

/* style for image */
.image {
  width: 100%;
}

.image img {
  width: inherit;
}

/* style for info */
.info {
  position: absolute;
  left: 5%;
  top: 50%;
  transform: translate(0, -50%);
}

.info h1 {
  margin: 1rem 0;
}

/* style for change screen size */

@media (width < 768px) {
  .container {
    width: var(--width-full);
  }
}
