/* style for header navbar */
.header {
  width: 100%;
  font-weight: 500;
}

/* style for nav */
.header nav {
  width: var(--width-large);
  margin: 0 auto;
  padding: 2rem;

  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* style for h2 */
.header h2 {
  letter-spacing: 0.2rem;
}

/* style for ul */
.header ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.header li {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* style for link */
.header a {
  text-decoration: none;
  color: inherit;
  font-size: 1.8rem;

  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.header a:hover,
.header a:active,
.header a.active {
  color: #e1bb6e;
}

.header a i,
.header p i {
  color: #aaaaaa;
}

/* style for logout action */
.header p {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.header p button {
  border: none;
  font-size: 1.8rem;
  font-style: italic;
  background: none;
  cursor: pointer;
}

.header p button:hover {
  color: #e1bb6e;
}

/* style for change screen size */

@media (width < 768px) {
  .header nav {
    width: var(--width-full);
  }
}

@media (width < 620px) {
  .header nav {
    flex-direction: column;
  }
}
