/* style for footer */
.footer {
  width: var(--width-full);
  color: #fff;
  background-color: #000;
}

.footer section {
  width: var(--width-small);
  margin: 0 auto;
  padding: 5rem 2rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* style for each block */
.footer section > div {
  padding: 1rem 0;
}

.footer h4 {
  font-weight: 400;
  margin-bottom: 2rem;
}

/* style for unorderd list */
.footer ul {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}

.footer a {
  text-decoration: none;
  font-size: 1.6rem;
  /* color: #4c5052; */
  color: rgba(200, 203, 204, 0.5);
}

.footer a:hover,
.footer a:active {
  color: rgb(231, 233, 234);
}

/* style for change screen size */
@media (width < 1200px) {
  .footer section {
    width: var(--width-medium);
  }
}

@media (width < 992px) {
  .footer section {
    width: var(--width-large);
  }
}

@media (width < 768px) {
  .footer section {
    width: var(--width-full);
  }
}
